body {
    margin: 0;
    font-family: "Inter", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #e5e5e5;
}

.wrapper {
    background: #ffffff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 20px;
    max-width: 1080px;
    margin: 50px auto;
}

.header {
    border-bottom: 1px solid #eaeaea;
    padding: 40px 40px 20px;
    &__logo-block {
        width: 100px;
        height: 100px;
        background-color: #000;
        border-radius: 50%;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__logo-block h5 {
        text-transform: uppercase;
    }
    &__desc {
        margin: 0;
        padding: 0;
    }
    &__panel {
        margin: 0;
        padding: 0;
        list-style: none;
    }
}

.content {
    padding: 20px 40px 40px;
    &__title {
        margin: 0;
        padding: 0 0 30px;
    }
}

.card {
    width: 210px;
    border: 1px solid #f3f3f3;
    padding: 30px;
    border-radius: 40px;
    box-sizing: border-box;
    margin-right: 40px;
    span {
        font-size: 13px;
        text-transform: uppercase;
        opacity: 0.8;
    }
    b {
        font-size: 12px;
    }
}

.add-btn {
    width: 32px;
    height: 32px;
    background: #ffffff;
    border: 1px solid #f2f2f2;
    box-sizing: border-box;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
    &:hover {
        background-color: #f0f0f0;
    }
}
